// "_img_carousel.scss" <- SASS

img_carousel_track = document.querySelector(".img-carousel__track");
move_left_btn = document.querySelector(".slide-left-btn");
move_right_btn = document.querySelector(".slide-right-btn");


// Move track one slide to the left/right until next slide snaps into place
move_right_btn.addEventListener('click', e => {
    img_carousel_track.scrollLeft += 194;
    //console.log(img_carousel_track.scrollLeft);
})

move_left_btn.addEventListener('click', e => {
    img_carousel_track.scrollLeft -= 194;
    //console.log(img_carousel_track.scrollLeft);
})